import { useState } from 'react';
import {
	Box,
	Button,
	Grid,
	makeStyles,
	Typography,
	Fade,
} from '@material-ui/core';
import { Display } from './Display';
import { Editor } from './Editor';
import { TAvailability } from '../../../../../../types';

const useStyles = makeStyles(theme => ({
	wrapper: {
		backgroundColor: theme.palette.grey['100'],
	},
	editButton: {
		...theme.typography.h5,
		fontWeight: 600,
		minWidth: 'unset',
		padding: '0.25rem 0.5rem',
		textTransform: 'capitalize',
	},
	saveButton: {
		...theme.typography.h4,
		fontWeight: 600,
		padding: theme.spacing(2, 4),
		color: theme.palette.common.white,
		borderWidth: '2px',
		textTransform: 'none',
	},
	saveButtonDisabled: {
		'&&': {
			backgroundColor: theme.palette.grey['100'],
		},
	},
}));

type Props = {
	availability: TAvailability;
	updateAvailability: (value: TAvailability) => void;
	triggerChange: () => void;
	defaultSetting: { start: string; end: string };
};

export const Availability = ({
	availability,
	updateAvailability,
	triggerChange,
	defaultSetting,
}: Props) => {
	const classes = useStyles();
	const [edit, setEdit] = useState(false);

	return (
		<Box bgcolor='grey.100' sx={{ p: 2, mb: 2, borderRadius: 0.5 }}>
			<Grid container justifyContent='space-between' alignItems='center'>
				<Grid item>
					<Typography variant='h4'>Set available hours</Typography>
				</Grid>
				<Grid item>
					<Button
						classes={{ root: classes.editButton }}
						onClick={() => setEdit(prevEdit => !prevEdit)}
					>
						{edit ? 'Done' : 'Edit'}
					</Button>
				</Grid>
			</Grid>
			<Box sx={{ mt: 2 }}>
				<Fade in={edit} timeout={500}>
					<div>
						{edit && (
							<Editor
								defaultSetting={defaultSetting}
								availability={availability}
								onUpdate={updateAvailability}
								triggerChange={triggerChange}
							/>
						)}
					</div>
				</Fade>
				<Fade in={!edit} timeout={500}>
					<div>
						{!edit && (
							<Display availability={availability} onEdit={() => setEdit(true)} />
						)}
					</div>
				</Fade>
			</Box>
		</Box>
	);
};
