import { Link, Typography, makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import config from '../../../../../config';
import { CopyToClipboard } from '../../../components/CopyToClipboard';

const useStyles = makeStyles(theme => ({
	disable: {
		pointerEvents: 'none',
		color: theme.palette.grey['400'],
	},
}));

const ON_MARKET_STATUS = 3;

type Props = {
	externalId: string;
	status: number;
};

export const PropertyExternalUrl = ({ externalId, status }: Props) => {
	const classes = useStyles();
	const url = `${config.dispatcherUrl}/${externalId}`;
	const isOnMarket = status === ON_MARKET_STATUS;

	return (
		<div>
			{Boolean(externalId) && (
				<CopyToClipboard
					content={url}
					show={
						<Link
							color='primary'
							variant='body1'
							href={url}
							target='_blank'
							rel='noopener noreferrer'
							classes={{ root: clsx({ [classes.disable]: !isOnMarket }) }}
						>
							{url}
						</Link>
					}
				/>
			)}
			{externalId && !isOnMarket && (
				<Typography variant='h5' color='error'>
					Property is not yet listed
				</Typography>
			)}
		</div>
	);
};
