import { Link } from '@material-ui/core';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { CopyToClipboard } from '../../../components/CopyToClipboard';

type Props = {
	url: string;
};

const CalendarUrl = ({ url }: Props) => (
	<SubCard title='Home Viewing Booking URL'>
		{Boolean(url) && (
			<CopyToClipboard
				content={url}
				show={
					<Link
						color='primary'
						variant='body1'
						href={url}
						target='_blank'
						rel='noopener noreferrer'
					>
						{url}
					</Link>
				}
			/>
		)}
	</SubCard>
);

export default CalendarUrl;
