import { CALENDAR_ACTIONS } from './constants';
import { TAppointment } from '../../../../../../types/YouCanBook';
import { convertAppointmentsTime } from '../helpers';

export const setConfig = (data: Object) => ({
	type: CALENDAR_ACTIONS.setConfig,
	payload: data,
});

export const setAvailability = (data: Object) => ({
	type: CALENDAR_ACTIONS.setAvailability,
	payload: data,
});

export const setBreakTime = (data: Object) => ({
	type: CALENDAR_ACTIONS.setBreakTime,
	payload: data,
});

export const setStartOfWeek = (data: string) => ({
	type: CALENDAR_ACTIONS.setStartOfWeek,
	payload: data,
});

export const setEndOfWeek = (data: string) => ({
	type: CALENDAR_ACTIONS.setEndOfWeek,
	payload: data,
});

export const setError = (data: Object | Error) => ({
	type: CALENDAR_ACTIONS.setError,
	payload: data,
});

export const setIsLoading = (data: boolean) => ({
	type: CALENDAR_ACTIONS.setIsLoading,
	payload: data,
});

export const setAppointments = (data: Array<TAppointment>) => ({
	type: CALENDAR_ACTIONS.setAppointments,
	payload: convertAppointmentsTime(data),
});

export const addAppointments = (data: Array<TAppointment>) => ({
	type: CALENDAR_ACTIONS.addAppointments,
	payload: convertAppointmentsTime(data),
});

export const setProperty = (data: Object) => ({
	type: CALENDAR_ACTIONS.setProperty,
	payload: data,
});

export const beginSelectingCell = (date: Date | undefined) => ({
	type: CALENDAR_ACTIONS.beginSelectingCell,
	payload: date,
});

export const stopSelectingCell = () => ({
	type: CALENDAR_ACTIONS.stopSelectingCell,
});

export const addSelectedCell = (date: Date | undefined) => ({
	type: CALENDAR_ACTIONS.addSelectedCell,
	payload: date,
});

export const setClientTimeZone = (data: string) => ({
	type: CALENDAR_ACTIONS.setClientTimeZone,
	payload: data,
});
