import {
	TDocSetting,
	TMappedSettings,
	TDocTypeSetting,
	TMappedDocTypes,
} from './types';

const UNCATEGORIZED_DOC_TYPE = 'Uncategorized';

export const mapSimpleSettings = (
	settings: Array<TDocSetting>
): TMappedSettings =>
	settings.reduce(
		(result, setting) => ({ ...result, [setting.id]: setting.name }),
		{}
	);

export const mapDocTypes = (
	docTypes: Array<TDocTypeSetting>
): TMappedDocTypes =>
	docTypes.reduce(
		(result, type) =>
			type.id === UNCATEGORIZED_DOC_TYPE ? result : { ...result, [type.id]: type },
		{}
	);
