import React from 'react';
import {
	Box,
	Checkbox,
	FormControlLabel,
	makeStyles,
	Button,
	Grid,
} from '@material-ui/core';
import clsx from 'clsx';
import { hasDay } from '../helpers';
import { TimeInput } from '../../../../components/TimeInput';
import { TAvailability, TDay } from '../../../../../../types';

const useStyles = makeStyles(theme => ({
	day: {
		backgroundColor: 'white',
		border: `1px solid ${theme.palette.grey['300']}`,
		'&:first-child': {
			borderRadius: '4px 4px 0 0',
		},
		'&:last-child': {
			borderRadius: '0 0 4px 4px',
		},
	},
	active: {
		'&:hover $copyButton': {
			display: 'block',
		},
	},
	dayLabel: {
		marginRight: theme.spacing(1.5),
	},
	name: {
		...theme.typography.h5,
		textTransform: 'capitalize',
		width: '2rem',
	},
	timeInput: {
		height: 'auto',
		width: '75px',
		textAlign: 'center',
	},
	copyButton: {
		...theme.typography.h5,
		padding: theme.spacing(0.5),
		textTransform: 'none',
		display: 'none',
	},
	disable: {
		color: theme.palette.grey['400'],
	},
}));

type Props = {
	availability: TAvailability;
	defaultSetting: { start: string; end: string };
	onUpdate: (value: TAvailability) => void;
	triggerChange: () => void;
};

export const Editor = ({
	availability,
	defaultSetting,
	onUpdate,
	triggerChange,
}: Props) => {
	const classes = useStyles();

	const onCheck =
		(day: string) => (event: React.ChangeEvent<HTMLInputElement>) => {
			const value = event.target.checked;
			onUpdate({
				...availability,
				[day]: value ? { name: day, ...defaultSetting } : { name: day },
			});
			triggerChange();
		};

	const onChangeTime =
		(day: string) => (event: React.ChangeEvent<HTMLInputElement>) => {
			const { value, name: time } = event.target;
			onUpdate({
				...availability,
				[day]: { ...availability[day], [time]: value },
			});
			triggerChange();
		};

	const onCopyToAll = (day: TDay) => () => {
		const { start, end } = day;
		const activeDays = Object.values(availability).filter((dayObj: TDay) =>
			hasDay(dayObj)
		);
		const activeDaysWithNewTime = activeDays.map(({ name }: TDay) => ({
			[name]: { name, start, end },
		}));
		onUpdate({
			...availability,
			...Object.assign({}, ...activeDaysWithNewTime),
		});
		triggerChange();
	};

	return (
		<Box sx={{ display: 'flex', flexDirection: 'column' }}>
			{Object.values(availability).map((day: TDay) => {
				const checked = hasDay(day);
				return (
					<Box
						key={day.name}
						className={clsx(classes.day, { [classes.active]: checked })}
						sx={{
							px: 2,
							py: 0.5,
							mb: '-1px',
							display: 'flex',
							alignItems: 'center',
							justifyContent: 'space-between',
						}}
					>
						<Box
							sx={{
								display: 'flex',
								alignItems: 'center',
							}}
						>
							<FormControlLabel
								control={
									<Checkbox
										color='primary'
										checked={checked}
										onChange={onCheck(day.name)}
									/>
								}
								classes={{
									root: classes.dayLabel,
									label: clsx(classes.name, {
										[classes.disable]: !checked,
									}),
								}}
								label={day.name}
							/>
							{checked && (
								<Grid container alignItems='center'>
									<TimeInput
										name='start'
										classes={{ input: classes.timeInput }}
										value={day.start || ''}
										onChange={onChangeTime(day.name)}
									/>
									
									<TimeInput
										name='end'
										classes={{ input: classes.timeInput }}
										value={day.end || ''}
										onChange={onChangeTime(day.name)}
									/>
								</Grid>
							)}
						</Box>
						<Button classes={{ root: classes.copyButton }} onClick={onCopyToAll(day)}>
							Copy to all
						</Button>
					</Box>
				);
			})}
		</Box>
	);
};
