import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import _get from 'lodash.get';
import { ProfilesService } from '../../../../../../services/YouCanBook';
import {
	setConfig,
	setAvailability,
	setBreakTime,
	setError,
	setIsLoading,
	setProperty,
} from '../redux/actions';
import {
	convertAvailability,
	convertBreakTime,
	convertAvailabilityToSave,
	convertBreakTimeToSave,
} from '../helpers';
import { TProperty } from '../../../../../../types';

const CALENDAR_CONFIG_FIELDS =
	'times,times.*,questions,questions.*,services, services.*,services.items.*,actions,actions.*,title,timeZone';
const CALENDAR_TIMES_CONFIG_FIELD = 'times';
const CALENDAR_TIMEZONE_CONFIG_FIELD = 'timeZone';

const useConfig = (property: TProperty) => {
	const dispatch = useDispatch();
	const config = useSelector(state => _get(state, ['calendar', 'config']));
	const availability = useSelector(state =>
		_get(state, ['calendar', 'availability'])
	);
	const breakTime = useSelector(state => _get(state, ['calendar', 'breakTime']));
	const profileId = _get(property, ['HomeViewingCalendarProfileId']) || '';
	const clientTimeZone = useSelector(state =>
		_get(state, ['calendar', 'clientTimeZone'])
	);

	useEffect(() => {
		(async () => {
			if (!profileId) return;
			dispatch(setProperty(property));
			await executeProfileRequest(false);
		})();

		return () => {
			ProfilesService.cancel();
		};
		// eslint-disable-next-line
	}, [profileId]);

	useEffect(() => {
		dispatch(setConfig(config));
		dispatch(setAvailability(convertAvailability(config)));
		dispatch(setBreakTime(convertBreakTime(config)));
	}, [clientTimeZone]);

	const triggerSaveSettings = async () => {
		await executeProfileRequest(true);
	};

	const executeProfileRequest = async (isOnSave = false) => {
		dispatch(setIsLoading(true));
		try {
			const data = isOnSave
				? await ProfilesService.updateProfileById({
						id: profileId,
						fields: CALENDAR_CONFIG_FIELDS,
						profile: {
							times: {
								...config.times,
								...convertAvailabilityToSave(availability, config.timeZone),
								...convertBreakTimeToSave(breakTime, config.timeZone),
							},
						},
				  })
				: await ProfilesService.getProfileById({
						id: profileId,
						fields: CALENDAR_CONFIG_FIELDS,
				  });

			const newConfig =
				{
					...data,
					..._get(data, CALENDAR_TIMES_CONFIG_FIELD),
					timeZone: _get(data, CALENDAR_TIMEZONE_CONFIG_FIELD),
				} || {};
			dispatch(setConfig(newConfig));
			dispatch(setAvailability(convertAvailability(newConfig)));
			dispatch(setBreakTime(convertBreakTime(newConfig)));
		} catch (error) {
			dispatch(setError(error as Error));
		}
		dispatch(setIsLoading(false));
	};

	return [triggerSaveSettings];
};

export default useConfig;
