import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import _get from 'lodash.get';
import { setError, setAppointments, setIsLoading } from '../redux/actions';
import { BookingsService } from '../../../../../../services/YouCanBook';

type Props = {
	profileId: string;
};

const useAppointments = ({ profileId }: Props) => {
	const dispatch = useDispatch();
	const startOfWeek = useSelector(state =>
		_get(state, ['calendar', 'startOfWeek'])
	);
	const endOfWeek = useSelector(state => _get(state, ['calendar', 'endOfWeek']));
	const appointments = useSelector(state =>
		_get(state, ['calendar', 'appointments'])
	);
	const clientTimeZone = useSelector(state =>
		_get(state, ['calendar', 'clientTimeZone'])
	);

	useEffect(() => {
		(async () => {
			if (!endOfWeek) return;

			try {
				dispatch(setIsLoading(true));

				const appointmentData = await BookingsService.getBookings({
					profileId,
					from: startOfWeek,
					to: endOfWeek,
				});
				dispatch(setAppointments(appointmentData));
			} catch (error) {
				dispatch(setError(error as Error));
			}
			dispatch(setIsLoading(false));
		})();

		return () => {
			BookingsService.cancel();
		};
	}, [endOfWeek, profileId]);

	useEffect(() => {
		dispatch(setAppointments(appointments));
	}, [clientTimeZone]);
};

export default useAppointments;
