import { List } from '@material-ui/core';
import _get from 'lodash.get';
import TextItem from '../../../components/TextItem';
import { TProperty } from '../../../../../types';
import SubCard from '../../../../../ui-component/cards/SubCard';

type Props = {
	property: TProperty;
};

export const PropertyInformation = ({ property }: Props) => (
	<SubCard title='Property Information'>
		<List>
			<TextItem<TProperty>
				data={property}
				source='PropertyNo'
				label='Property No'
			/>
			<TextItem<TProperty>
				data={property}
				source='PropertyData.StreetAddress'
				label='Address'
			/>
			<TextItem<TProperty>
				data={property}
				source='PropertyData.OwnerNames'
				label='Owner(s)'
			/>
			<TextItem<TProperty>
				data={property}
				getValue={row =>
					_get(row, 'PropertyData.VestingTypeMapped', '') ||
					_get(row, 'PropertyData.VestingTypeOverride', '')
				}
				label='Vesting Type'
			/>
			<TextItem<TProperty>
				data={property}
				getValue={row =>
					_get(row, 'PropertyData.YearBuilt', '') ||
					_get(row, 'PropertyData.YearBuiltOverride', '')
				}
				label='Year Built'
				defaultValue='N/A'
			/>
		</List>
	</SubCard>
);
