import * as yup from 'yup';
import {
	Grid,
	Typography,
	Button,
	Dialog,
	DialogActions,
	DialogContent,
	DialogTitle,
	useMediaQuery,
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import { FileDownload as FileDownloadIcon } from '@material-ui/icons';
import { memo, useEffect } from 'react';
import { Formik } from 'formik';
import _isEmpty from 'lodash.isempty';
import {
	TDocSetting,
	TEditDoc,
	TMappedSettings,
	TMappedDocTypes,
} from '../types';
import DocumentTypes from './DocumentTypes';
import DocumentStatuses from './DocumentStatuses';
import DocumentPermissions from './DocumentPermissions';
import DocumentOffers from './DocumentOffers';
import { gridSpacing } from '../../../../../../store/constant';
import { TOffer } from '../../../../../../types';
import useUploadDocs from './useUploadDocs';
import UploadFile from '../../../../components/UploadFile';
import Loader from '../../../../../../ui-component/Loader';
import config from '../../../../../../config';

type Props = {
	propertyId: string;
	openDialog: boolean;
	handleCloseDialog: () => void;
	selectedDoc: TEditDoc;
	docTypes: TMappedDocTypes;
	docPermissions: Array<TDocSetting>;
	docStatuses: TMappedSettings;
	offers: Array<TOffer>;
	isEditing: boolean;
	handleDownloadDocs: Function;
	isDownloading: boolean;
};

const validationEditSchema = yup.object().shape({
	docType: yup.string().required('Document type is required'),
	docStatus: yup.string().required('Document status is required'),
	docPermissions: yup
		.array()
		.min(1, 'Document permissions are required')
		.required('Document permissions are required'),
});

const DocumentDialog = ({
	propertyId,
	openDialog,
	handleCloseDialog,
	docTypes,
	docPermissions,
	docStatuses,
	selectedDoc,
	offers,
	isEditing,
	handleDownloadDocs,
	isDownloading,
}: Props) => {
	const theme = useTheme();
	const matchMobile = useMediaQuery(theme.breakpoints.down('sm'));
	const { files, onFilesChange, uploadFiles, isUploadLoading, handleUpdateDoc } =
		useUploadDocs({
			propertyId,
			handleCloseDialog,
		});

	useEffect(() => {
		onFilesChange([]);
	}, [openDialog]);

	const onSubmitHandler = (values: TEditDoc) => {
		if (isEditing) {
			handleUpdateDoc(values);
			return;
		}

		if (_isEmpty(files)) return;

		uploadFiles(values);
	};

	const getFilesComponent = () =>
		isEditing ? (
			<Grid>
				<Button
					variant='outlined'
					startIcon={<FileDownloadIcon />}
					sx={{ textTransform: 'none' }}
					disabled={isDownloading}
					onClick={() => {
						handleDownloadDocs(selectedDoc.objectKey);
					}}
				>
					Download document
				</Button>
			</Grid>
		) : (
			<UploadFile
				name='docs'
				files={files}
				onDrop={onFilesChange}
				loading={isUploadLoading}
				accept={config.uploadFile.acceptedTypes}
			/>
		);

	return (
		<>
			{isUploadLoading && <Loader />}
			<Dialog
				fullScreen={matchMobile}
				open={openDialog}
				aria-labelledby='dialog-title'
				fullWidth
				scroll='paper'
			>
				<DialogTitle id='dialog-title'>
					<Typography variant='h4'>
						{isEditing ? selectedDoc.file?.name : 'Upload new documents'}
					</Typography>
				</DialogTitle>
				<Formik
					initialValues={selectedDoc}
					onSubmit={onSubmitHandler}
					enableReinitialize
					validationSchema={validationEditSchema}
				>
					{({ handleSubmit }) => (
						<form noValidate onSubmit={handleSubmit}>
							<DialogContent>
								<Grid
									container
									spacing={gridSpacing}
									alignItems='flex-start'
									justifyContent='space-between'
									direction='row'
								>
									<Grid item xs={12}>
										{getFilesComponent()}
									</Grid>
									<DocumentTypes docTypes={docTypes} />
									<DocumentPermissions docPermissions={docPermissions} />
									<DocumentStatuses docStatuses={docStatuses} />
									<DocumentOffers docOffers={offers} isEditing={isEditing} />
								</Grid>
							</DialogContent>
							<DialogActions>
								<Button autoFocus onClick={handleCloseDialog}>
									Cancel
								</Button>
								<Button
									autoFocus
									color='primary'
									variant='contained'
									type='submit'
									disabled={isUploadLoading}
								>
									Save
								</Button>
							</DialogActions>
						</form>
					)}
				</Formik>
			</Dialog>
		</>
	);
};

export default memo(DocumentDialog);
