import { useState } from 'react';
import { Button, makeStyles } from '@material-ui/core';
import _get from 'lodash.get';
import { useSelector, useDispatch } from 'react-redux';
import _isEmpty from 'lodash.isempty';
import { DEFAULT_SETTING } from './constants';
import { BreakTime } from './BreakTime';
import { Availability } from './Availability';
import { TAvailability } from '../../../../../types';
import MainCard from '../../../../../ui-component/cards/MainCard';
import { setAvailability } from './redux/actions';

const useStyles = makeStyles(theme => ({
	wrapper: {
		backgroundColor: theme.palette.grey['100'],
	},
	editButton: {
		...theme.typography.h5,
		fontWeight: 600,
		minWidth: 'unset',
		padding: '0.25rem 0.5rem',
		textTransform: 'capitalize',
	},
	saveButton: {
		...theme.typography.h4,
		fontWeight: 600,
		padding: theme.spacing(2, 4),
		color: theme.palette.common.white,
		borderWidth: '2px',
		textTransform: 'none',
	},
	saveButtonDisabled: {
		'&&': {
			backgroundColor: theme.palette.grey['100'],
		},
	},
}));

type Props = {
	triggerSaveSettings: Function;
};

export const Settings = ({ triggerSaveSettings }: Props) => {
	const classes = useStyles();
	const dispatch = useDispatch();
	const [canSave, setCanSave] = useState(false);

	const availability = useSelector(state =>
		_get(state, ['calendar', 'availability'])
	);

	if (_isEmpty(availability)) return null;

	const onSubmit = async () => {
		setCanSave(false);
		await triggerSaveSettings();
		setCanSave(true);
	};

	const updateAvailability = (value: TAvailability) => {
		dispatch(setAvailability(value));
	};

	const triggerChange = () => setCanSave(true);

	return (
		<MainCard title=''>
			<Availability
				availability={availability}
				updateAvailability={updateAvailability}
				triggerChange={triggerChange}
				defaultSetting={DEFAULT_SETTING}
			/>
			<BreakTime triggerChange={triggerChange} />
			<Button
				fullWidth
				disableElevation
				color='primary'
				variant='contained'
				classes={{
					root: classes.saveButton,
					disabled: classes.saveButtonDisabled,
				}}
				disabled={!canSave}
				onClick={onSubmit}
			>
				Save changes
			</Button>
		</MainCard>
	);
};
