import {
	TableContainer,
	Stepper,
	Step,
	StepLabel,
	Button,
	StepContent,
	Typography,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import SubCard from '../../../../ui-component/cards/SubCard';
import { listPropertyStatuses } from '../../../../graphql/queries';
import { TPropertyStatus, TProperty } from '../../../../types';
import Loader from '../../../../ui-component/Loader';

const STEP_LIMIT = 7;
const ONBOARDING_STEP_ID = 1;

type Props = {
	isUpdatePropertyStatus: boolean;
	status: number;
	onClick: (nextStep: number) => void;
	property: TProperty;
};

const useStyles = makeStyles(theme => ({
	stepper: {
		padding: 0,
	},
	icon: {
		color: theme.palette.grey[500],
		'&$activeIcon': {
			color: '#5BB573',
		},
		'&$completedIcon': {
			color: '#5BB573',
		},
	},
	actionsContainer: {
		marginTop: theme.spacing(2),
	},
	activeIcon: {},
	completedIcon: {},
}));

const Status = ({
	isUpdatePropertyStatus,
	status,
	onClick,
	property,
}: Props) => {
	const classes = useStyles();
	const fullAddress = _get(property, 'PropertyData.FullAddress', '');
	const formStatuses = _get(property, 'PropertyFormDataStatuses') || [];
	const submittedForm = _isEmpty(formStatuses)
		? 0
		: formStatuses.filter((form: { FormDataSubmitted: boolean }) =>
				_get(form, 'FormDataSubmitted')
		  ).length;

	const { loading, error, data } = useQuery(gql(listPropertyStatuses));

	if (loading || isUpdatePropertyStatus) {
		return <Loader />;
	}

	if (loading || error || _isEmpty(data)) {
		return <></>;
	}

	const steps: Array<{ label: string; description: string; id: number }> = _get(
		data,
		'listPropertyStatuses',
		[]
	)
		.slice(1, STEP_LIMIT)
		.map((status: TPropertyStatus) => ({
			label: _get(status, 'Name'),
			description: _get(status, 'Desc'),
			id: _get(status, 'Id'),
		}));

	const onboardingFormStatus = `(${submittedForm}/${formStatuses.length})`;

	return (
		<SubCard title='Home Sale Status'>
			<TableContainer>
				<p>
					Current status of your property <strong>{fullAddress}</strong>
				</p>
				<Stepper
					className={classes.stepper}
					activeStep={status - 1}
					orientation='vertical'
				>
					{steps.map(({ label, description, id }) => (
						<Step key={label}>
							<StepLabel
								StepIconProps={{
									classes: {
										root: classes.icon,
										active: classes.activeIcon,
										completed: classes.completedIcon,
									},
								}}
							>
								{label} {id === ONBOARDING_STEP_ID ? onboardingFormStatus : ''}
							</StepLabel>
							<StepContent>
								<Typography>{description}</Typography>
								<div className={classes.actionsContainer}>
									{status > ONBOARDING_STEP_ID && (
										<Button onClick={() => onClick(status - 1)}>Back</Button>
									)}
									{status < STEP_LIMIT && (
										<Button
											onClick={() => onClick(status + 1)}
											variant='contained'
											color='primary'
										>
											Next
										</Button>
									)}
								</div>
							</StepContent>
						</Step>
					))}
				</Stepper>
			</TableContainer>
		</SubCard>
	);
};

export default Status;
