import { useTheme, Alert } from '@material-ui/core';
import { useEffect } from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { makeStyles } from '@material-ui/styles';
import Loader from '../../../ui-component/Loader';
import MainCard from '../../../ui-component/cards/MainCard';
import Form from './components/Form';
import { shouldStopPolling } from './Helper';

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

const PropertyEdit = ({
	loading: isLoading,
	error,
	data,
	refetch,
	stopPolling,
}: any) => {
	const theme = useTheme();
	const classes = useStyles();

	const property = _get(data, 'getProperty', {});
	const isStop = shouldStopPolling(property);

	useEffect(() => {
		if (isStop) stopPolling();
	}, [isStop]);

	if (error && _isEmpty(property)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Property not found
			</Alert>
		);
	}

	return (
		<MainCard content={false} className={classes.mainCard}>
			{isLoading && <Loader />}
			{!isLoading && !_isEmpty(data) && <Form formData={data} refetch={refetch} />}
		</MainCard>
	);
};

export default PropertyEdit;
