import React, { useEffect, useRef } from 'react';
import {
	IconButton,
	InputAdornment,
	makeStyles,
	TextField,
} from '@material-ui/core';
import Inputmask from 'inputmask';
import clsx from 'clsx';
import ClearIcon from '@material-ui/icons/Clear';
import { TIME_REGEX } from '../../../../helpers/date';

const useStyles = makeStyles(theme => ({
	root: {
		background: theme.palette.background.paper,
		paddingRight: 0,
	},
	hasIcon: {
		paddingRight: theme.spacing(0.5),
	},
	input: {
		...theme.typography.h5,
		padding: theme.spacing(1),
		textTransform: 'uppercase',
	},
	icon: {
		padding: theme.spacing(0.75),
	},
}));

type Props = {
	name?: string;
	value: string;
	classes?: { [key: string]: string };
	onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
	resetValue?: () => void;
};

export const TimeInput = ({
	name = '',
	value = '',
	classes: styleClasses,
	onChange,
	resetValue,
}: Props) => {
	const classes = useStyles();
	const ref = useRef<HTMLInputElement>();

	useEffect(() => {
		if (ref.current) {
			const inputElm = ref.current.getElementsByTagName('input')[0];
			Inputmask({ regex: TIME_REGEX }).mask(inputElm);
		}	
	}, []);

	return (
		<TextField
			color="secondary"
			placeholder='10:00 AM'
			name={name}
			value={value}
			onChange={onChange}
			variant='outlined'
			classes={{ root: styleClasses?.root }}
			InputProps={{
				ref,
				classes: {
					root: clsx(classes.root, {
						[classes.hasIcon]: Boolean(resetValue),
					}),
					input: clsx(classes.input, styleClasses?.input),
				},
				endAdornment: Boolean(resetValue) && (
					<InputAdornment position='end'>
						<IconButton classes={{ root: classes.icon }} onClick={resetValue}>
							<ClearIcon fontSize='small' />
						</IconButton>
					</InputAdornment>
				),
			}}
		/>
	);
};
