import { Grid, CardContent, makeStyles } from '@material-ui/core';
import { useSelector } from 'react-redux';
import _get from 'lodash.get';
import { Settings } from './Settings';
import Loader from '../../../../../ui-component/Loader';
import useError from './hooks/useError';
import useConfig from './hooks/useConfig';
import useAppointments from './hooks/useAppointments';
import Scheduler from './Scheduler';
import CalendarUrl from './CalendarUrl';
import { TProperty } from '../../../../../types';
import { gridSpacing } from '../../../../../store/constant';
import MainCard from '../../../../../ui-component/cards/MainCard';

type Props = {
	property: TProperty;
};

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

const Calendar = ({ property }: Props) => {
	const classes = useStyles();
	const isLoading = useSelector(state => _get(state, ['calendar', 'isLoading']));
	const [triggerSaveSettings] = useConfig(property);
	const profileId = _get(property, 'HomeViewingCalendarProfileId');
	const calendarUrl = _get(property, 'CalendarUrl');
	useAppointments({ profileId });
	useError();

	return (
		<>
			{isLoading && <Loader />}
			<MainCard content={false} className={classes.mainCard}>
				<CardContent>
					<Grid
						container
						spacing={gridSpacing}
						justifyContent='center'
						direction='row'
					>
						<Grid item xs={12}>
							<Grid
								container
								direction='row'
								alignItems='stretch'
								spacing={gridSpacing}
							>
								<Grid item xs={12} md={12} lg={6}>
									<Settings triggerSaveSettings={triggerSaveSettings} />
								</Grid>
								<Grid item xs={12} md={12} lg={6}>
									{!isLoading && <CalendarUrl url={calendarUrl} />}
								</Grid>
							</Grid>
						</Grid>
						<Grid item xs={12}>
							<Scheduler profileId={profileId} />
						</Grid>
					</Grid>
				</CardContent>
			</MainCard>
		</>
	);
};

export default Calendar;
