import { Button, ButtonGroup, Grid, makeStyles } from '@material-ui/core';
import _groupBy from 'lodash.groupby';
import { TAvailability, TDay } from '../../../../../../types';
import { UNAVAILABLE, UNAVAILABLE_GROUP } from '../constants';
import { displayDays, getDisplayTime } from '../helpers';

const useStyles = makeStyles(theme => ({
	group: {
		width: '100%',
	},
	groupButton: {
		padding: '1rem',
		backgroundColor: 'white',
		textTransform: 'none',
		textAlign: 'left',
		justifyContent: 'flex-start',
		'&:hover': {
			zIndex: 1,
		},
	},
	days: {
		color: theme.palette.grey['500'],
		textTransform: 'capitalize',
	},
	item: {
		...theme.typography.h5,
	},
}));

type Props = {
	availability: TAvailability;
	onEdit: () => void;
};

export const Display = ({ availability, onEdit }: Props) => {
	const classes = useStyles();
	const groups = _groupBy(availability, getDisplayTime);
	const unavailableDays = groups[UNAVAILABLE_GROUP];
	const entries = Object.entries(groups).filter(
		([group]) => group !== UNAVAILABLE_GROUP
	);

	return (
		<ButtonGroup
			color='secondary'
			orientation='vertical'
			classes={{ root: classes.group }}
		>
			{entries.map(([group, days]) => (
				<Button
					key={group}
					classes={{ root: classes.groupButton }}
					onClick={onEdit}
				>
					<Grid container justifyContent='space-between' className={classes.item}>
						<Grid item>{group}</Grid>
						<Grid item>
							<span className={classes.days}>{displayDays(days as TDay[])}</span>
						</Grid>
					</Grid>
				</Button>
			))}
			{Boolean(unavailableDays) && (
				<Button classes={{ root: classes.groupButton }} onClick={onEdit}>
					<Grid container justifyContent='space-between' className={classes.item}>
						<Grid item>{UNAVAILABLE}</Grid>
						<Grid item>
							<span className={classes.days}>
								{displayDays(unavailableDays as TDay[])}
							</span>
						</Grid>
					</Grid>
				</Button>
			)}
		</ButtonGroup>
	);
};
