import {
	Grid,
	makeStyles,
	CardContent,
} from '@material-ui/core';
import _get from 'lodash.get';
import { gridSpacing } from '../../../store/constant';
import {  TProperty } from '../../../types';
import MainCard from '../../../ui-component/cards/MainCard';
import {
	PropertyInformation,
	SellerInfo,
	ListingInformation,
  HomeSaleStatus,
} from './components/PropertyDetail';

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

type Props = {
	property: TProperty;
};

export const PropertyDetail = ({ property }: Props) => {
	const classes = useStyles();
	const sellerInfo = _get(property, 'SellerInfo');

	return (
		<MainCard content={false} className={classes.mainCard}>
			<CardContent>
				<Grid
					container
					spacing={gridSpacing}
					alignItems='flex-start'
					justifyContent='center'
				>
					<Grid item lg={6} xs={12}>
						<SellerInfo sellerInfo={sellerInfo} />
            <br/>
						<ListingInformation property={property} />
					</Grid>
					<Grid item lg={6} xs={12}>
						<PropertyInformation property={property} />
            <br/>
            <HomeSaleStatus  property={property}    />
					</Grid>
				</Grid>
			</CardContent>
		</MainCard>
	);
};
