import _isEmpty from 'lodash.isempty';
import type {
	TGetProfileRequest,
	TUpdateProfileRquest,
} from '../../types/YouCanBook';
import Request from './Request';
import { PATHS } from './constants';

class ProfilesService extends Request {
	getProfileById({ id, fields }: TGetProfileRequest) {
		if (!id) return {};

		return this.axios({
			method: 'GET',
			url: `${PATHS.PROFILES}/${id}`,
			params: {
				fields,
			},
		});
	}

	updateProfileById({ id, profile, fields }: TUpdateProfileRquest) {
		if (!id || _isEmpty(profile)) return {};

		return this.axios({
			method: 'PATCH',
			url: `${PATHS.PROFILES}/${id}`,
			params: {
				fields,
			},
			data: profile,
		});
	}
}

export default new ProfilesService();
