import { memo } from 'react';
import _get from 'lodash.get';
import { useFormikContext } from 'formik';
import {
	Grid,
	FormHelperText,
	InputLabel,
	MenuItem,
	Select,
} from '@material-ui/core';
import { TMappedSettings, TEditDoc } from '../types';
import { CustomMenuProps, useSelectStyles } from './styles';

type Props = {
	docStatuses: TMappedSettings;
};

const DocumentStatuses = ({ docStatuses }: Props) => {
	const classes = useSelectStyles();
	const { values, handleChange, touched, errors } = useFormikContext<TEditDoc>();

	const errorText = touched.docStatus && errors.docStatus;
	const hasError = Boolean(errorText);

	return (
		<Grid item xs={12}>
			<InputLabel
				htmlFor='document-status-label'
				className={classes.label}
				error={hasError}
			>
				Document status
			</InputLabel>
			<Select
				fullWidth
				labelId='document-status-label'
				id='docStatus'
				value={values.docStatus}
				onChange={handleChange('docStatus')}
				MenuProps={CustomMenuProps}
				error={hasError}
			>
				{Object.keys(docStatuses).map((key: string) => (
					<MenuItem key={key} value={key}>
						{_get(docStatuses, key, '')}
					</MenuItem>
				))}
			</Select>
			<FormHelperText id='document-status-label' error={hasError}>
				{errorText}
			</FormHelperText>
		</Grid>
	);
};

export default memo(DocumentStatuses);
