import {
	Stepper,
	Step,
	StepLabel,
	Tooltip,
	StepContent,
	Typography,
	IconButton,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import DownloadIcon from '@material-ui/icons/Download';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { listPropertyStatuses } from '../../../../../graphql/queries';
import { TProperty, TPropertyStatus } from '../../../../../types';
import Loader from '../../../../../ui-component/Loader';

const STEP_LIMIT = 7;
const ONBOARDING_STEP_ID = 1;

type Props = {
	property: TProperty;
};

const useStyles = makeStyles(theme => ({
	stepper: {
		padding: 0,
	},
	icon: {
		color: theme.palette.grey[500],
	},
	actionsContainer: {
		marginTop: theme.spacing(2),
	},
	stepLabel: {
		display: 'flex',
		alignItems: 'center',
	},
	export: {
		marginLeft: theme.spacing(1),
		padding: theme.spacing(1),
	},
}));

export const HomeSaleStatus = ({ property }: Props) => {
	const classes = useStyles();
	const { loading, data } = useQuery(gql(listPropertyStatuses));
	const fullAddress = _get(property, 'PropertyData.FullAddress', '');
	const status = _get(property, 'StatusId', 0);
	const presignedUrl = _get(property, ['PropertyReport', 'PresignedUrl'], '');
	const formStatuses = _get(property, 'PropertyFormDataStatuses') || [];
	const submittedForm = _isEmpty(formStatuses)
		? 0
		: formStatuses.filter((form: { FormDataSubmitted: boolean }) =>
				_get(form, 'FormDataSubmitted')
		  ).length;

	if (loading) {
		return <Loader />;
	}

	if (_isEmpty(data)) {
		return <></>;
	}

	const steps: Array<{ label: string; description: string; id: number }> = _get(
		data,
		'listPropertyStatuses',
		[]
	)
		.slice(1, STEP_LIMIT)
		.map((status: TPropertyStatus) => ({
			label: _get(status, 'Name'),
			description: _get(status, 'Desc'),
			id: _get(status, 'Id'),
		}));

	const onboardingFormStatus = `(${submittedForm}/${formStatuses.length})`;

	return (
		<SubCard title='Home Sale Status'>
			<div>
				<p>
					Current status of your property <strong>{fullAddress}</strong>
				</p>
				<Stepper
					className={classes.stepper}
					activeStep={status - 1}
					orientation='vertical'
				>
					{steps.map(({ label, description, id }) => (
						<Step key={label}>
							<StepLabel
								classes={{ label: classes.stepLabel }}
								StepIconProps={{
									classes: {
										root: classes.icon,
									},
								}}
							>
								{label} {id === ONBOARDING_STEP_ID ? onboardingFormStatus : ''}
								{id === ONBOARDING_STEP_ID &&
									status > ONBOARDING_STEP_ID &&
									!_isEmpty(presignedUrl) && (
										<Tooltip title='Export Seller Disclosure Questions'>
											<IconButton
												type='button'
												color='primary'
												href={presignedUrl}
												className={classes.export}
											>
												<DownloadIcon />
											</IconButton>
										</Tooltip>
									)}
							</StepLabel>
							<StepContent>
								<Typography>{description}</Typography>
							</StepContent>
						</Step>
					))}
				</Stepper>
			</div>
		</SubCard>
	);
};
