import { memo } from 'react';
import { useFormikContext } from 'formik';
import {
	Grid,
	FormHelperText,
	InputLabel,
	MenuItem,
	Select,
} from '@material-ui/core';
import { TEditDoc } from '../types';
import { TOffer } from '../../../../../../types';
import { CustomMenuProps, useSelectStyles } from './styles';

type Props = {
	docOffers: Array<TOffer>;
	isEditing: boolean;
};

const DocumentOffers = ({ docOffers, isEditing }: Props) => {
	const classes = useSelectStyles();
	const { values, handleChange, touched, errors } = useFormikContext<TEditDoc>();

	const errorText = touched.offerId && errors.offerId;
	const hasError = Boolean(errorText);

	return (
		<Grid item xs={12}>
			<InputLabel
				htmlFor='document-offer-label'
				className={classes.label}
				error={hasError}
			>
				Document offer
			</InputLabel>
			{isEditing ? (
				`${values.offerNo || ''}`
			) : (
				<>
					<Select
						fullWidth
						labelId='document-offer-label'
						id='offerId'
						value={values.offerId}
						onChange={handleChange('offerId')}
						MenuProps={CustomMenuProps}
						error={hasError}
					>
						{docOffers.map(offer => (
							<MenuItem key={offer.OfferId} value={offer.OfferId}>
								{offer.SellerAccepted ? `${offer.OfferNo} - Winning` : offer.OfferNo}
							</MenuItem>
						))}
					</Select>
					<FormHelperText id='document-offer-label' error={hasError}>
						{errorText}
					</FormHelperText>
				</>
			)}
		</Grid>
	);
};

export default memo(DocumentOffers);
