// @ts-nocheck
import { Grid, List } from '@material-ui/core';
import parsePhoneNumber from 'libphonenumber-js';
import _get from 'lodash.get';
import SubCard from '../../../../../ui-component/cards/SubCard';
import TextItem from '../../../components/TextItem';
import { TAddProperty } from '../../../../../types';

type Props = {
	sellerInfo: TAddProperty;
};

export const SellerInfo = ({ sellerInfo }: Props) => (
	<SubCard title='Seller Information'>
		<Grid item>
			<List>
				<TextItem<TAddProperty> data={sellerInfo} source='Email' label='Email' />
				<TextItem<TAddProperty>
					data={sellerInfo}
					source='FirstName'
					label='First Name'
				/>
				<TextItem<TAddProperty>
					data={sellerInfo}
					source='LastName'
					label='Last Name'
				/>
				<TextItem<TAddProperty>
					data={sellerInfo}
					label='Phone'
					getValue={(info: TAddProperty) => {
						const phoneNumber = parsePhoneNumber(`+${_get(info, 'Phone')}` || '');
						return phoneNumber ? phoneNumber.formatInternational() : '';
					}}
				/>
			</List>
		</Grid>
	</SubCard>
);
