import { List, makeStyles, CardContent, Grid } from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { TProperty, TReportStatus } from '../../../../types';
import MainCard from '../../../../ui-component/cards/MainCard';
import TextItem from '../../components/TextItem';
import { gridSpacing } from '../../../../store/constant';
import SubCard from '../../../../ui-component/cards/SubCard';

type Props = {
	property: TProperty;
};

const useStyles = makeStyles(theme => ({
	mainCard: {
		marginTop: theme.spacing(2),
		marginBottom: theme.spacing(2),
		border: 'none',
	},
}));

const Advanced = ({ property }: Props) => {
	if (_isEmpty(property)) return <></>;

	const classes = useStyles();
	const taxReportStatus = _get(property, 'ReportStatus', {});

	return (
		<MainCard title='' content={false} className={classes.mainCard}>
			<CardContent>
				<Grid
					container
					spacing={gridSpacing}
					alignItems='flex-start'
					justifyContent='flex-start'
				>
					<Grid item lg={6} xs={12}>
						<SubCard>
							<List>
								<TextItem<TReportStatus>
									data={taxReportStatus}
									source='TaxReportTaxResourceStatus'
									label='Tax Resource Status'
								/>
								<TextItem<TReportStatus>
									data={taxReportStatus}
									source='TaxReportTaxResourceMessage'
									label='Tax Resource Message'
								/>
								<TextItem<TReportStatus>
									data={taxReportStatus}
									source='TaxReportTotalViewStatus'
									label='Total View Status'
								/>
								<TextItem<TReportStatus>
									data={taxReportStatus}
									source='TaxReportTotalViewMessage'
									label='Total View Message'
								/>
								<TextItem<TProperty>
									data={property}
									source='PropertyData.VestingOwnershipRight'
									label='Original Vesting Type'
									defaultValue='N/A'
								/>
								<TextItem<TProperty>
									data={property}
									source='PropertyData.VestingTypeMapped'
									label='Vesting Type Mapped'
									defaultValue='N/A'
								/>
								<TextItem<TProperty>
									data={property}
									source='PropertyData.VestingTypeOverride'
									label='Vesting Type Override'
									defaultValue='N/A'
								/>
								<TextItem<TProperty>
									data={property}
									source='PropertyData.YearBuilt'
									label='Original Year Built'
									defaultValue='N/A'
								/>
								<TextItem<TProperty>
									data={property}
									source='PropertyData.YearBuiltOverride'
									label='Year Built Override'
									defaultValue='N/A'
								/>
							</List>
						</SubCard>
					</Grid>
				</Grid>
			</CardContent>
		</MainCard>
	);
};

export default Advanced;
